body {
  font: 30px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

h1{
  text-align: left;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: flex;
  justify-content: center;
  margin-right: 50px;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 3px solid lightslategray;
  float: right;
  font-size: 34px;
  font-weight: bold;
  line-height: 100px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 100px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  /*flex-direction: row;*/
  justify-content: center;
}
/*
.game-board{
  margin-top: 200px;
}*/

.game-info {
  text-align: center;
  margin-left: 25px;
}

button.square[data-pro="X"]{
  color: red;
}
button.square[data-pro="O"] {
  color: green;
}

.square--winning{
  background-color: yellowgreen;
}

