.header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 8vh;
}
.title{
  width: 55%;
}
.statistics{
  display: flex;
  justify-content: space-between;
  margin: 0;
  width: 50%;
}

.input_name{
  height: 30px;
}